    display: block;
    top: calc(100% + 5px);
    width: calc(50% - 24px);
    height: 2px;
    background: #00aaef; }
  .title::after {
    right: 2.5px; }
  .title::before {
    left: 2.5px; }
  .title__crown::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: calc(100% + 4px);
    margin: auto;
    background-image: url("../images/crown.svg");
    width: 20px;
    height: 13px; }

.subtitle {
